    <div class="btns mb10">
      <div @click="$exportDetail('/export/studentInfoExport.do',{id: $route.query.id})" v-if="editPage"
           class="btn-submit mr15">导出
      </div>
      <div v-if="!editPage && isReading" @click="doSubmit('add')" class="btn-submit btn-add mr15">提交</div>
      <div v-if="editPage && isReading" @click="doSubmit('save')" class="btn-submit btn-save mr15">保存</div>
      <div v-if="editPage && ($route.query.type === 'AT_SCHOOL' || $route.query.type === 'OTHER') && isReading"
           @click="showDialog('save')" class="btn-submit btn-save mr15">更换班级
      </div>
      <div @click="$router.go(-1)" class="btn-clear">返回</div>
    </div>
    <el-dialog width="540px" class="dialog" title="更换班级" :visible.sync="dialog">
      <div class="item">
        <span class="title imp">选择班级</span>
        <el-select v-model="changeClassId" default-first-option placeholder="请选择班级" filterable clearable class="input">
          <el-option
              v-for="(item,key) in classList"
              :value="item.classId" :label="item.aliasName" :key="key"
          ></el-option>
        </el-select>
      </div>
      <div class="btns">
        <div @click="changeClassFun" class="btn-submit btn-change mr15">确认</div>
        <div @click="dialog = false" class="btn-clear">取消</div>
      </div>
