</template>
<script>
  import mixins from '../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data() {
      return {
        // 是否为编辑页
        editPage: this.$route.path.includes('detail'),
        // 是否为在读生
        isReading: this.$route.path.startsWith('/edu-affair/reading'),
        // 民族列表
        nationList: [],
        // 班级列表
        classList: [],
        // 学生性质级联
        natureList: [],
        form: {
          avatar: null,
          studentStatus: 'INTERNAL_STUDENT',
          name: '',
          sex: null,
          usedName: '',
          idCard: '',
          birthday: null,
          isSchoolRoll: null,
          flowStudentNo: '',
          nationStudentNo: '',
          studentNature: [],
          primaryStudentNo: '',
          classStudentNo: '',
          classId: null,
          remark: '',
          nativePlace: '',
          nationId: null,
          residentType: null,
          enrollType: null,
          politicsStatus: null,
          isImmigrant: null,
          situationStatus: null,
          enrollmentSchool: '',
          residentRegionCode: '',
          residentRegion: '',
          currentAddressCode: '',
          currentAddress: '',
          speciality: '',
          isLeftover: null,
          disabilityStatus: null,
          isMigrantWorker: null,
          isSingle: null,
          isOverseas: null,
          isStaffChild: null,
          migrantWorkType: null,
          father: {
            name: '',
            company: '',
            phone: ''
          },
          mother: {
            name: '',
            company: '',
            phone: ''
          },
          guardian: {
            name: '',
            company: '',
            phone: ''
          },
        },
        rules: {
          name: {required: true, message: ' ', trigger: 'blur'},
          sex: {required: true, message: ' ', trigger: 'change'},
          idCard: {required: true, message: ' ', trigger: 'blur'},
          isSchoolRoll: {required: true, message: ' ', trigger: 'change'},
          studentNature: {required: true, message: ' ', trigger: 'change'},
          classId: {required: true, message: ' ', trigger: 'change'},
          birthday: {required: true, message: ' ', trigger: 'change'},
          flowStudentNo: {required: true, message: ' ', trigger: 'blur'},
          nativePlace: {required: true, message: ' ', trigger: 'blur'},
          nationId: {required: true, message: ' ', trigger: 'change'},
          residentType: {required: true, message: ' ', trigger: 'change'},
        },
        // 切换班级时的ID
        changeClassId: null,
        // 切换班级弹窗
        dialog: false
      }
    },
    methods: {
      // 上传封面图
      async uploadCover(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        if (file.size > 1024 * 1024 * 10) {
          this.$message.warning('封面图不能超过10MB')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/oss/upload.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        this.form.avatar = await this.$ajax(obj)
        this.$refs.uploadCover.value = null;
      },
      // 获取所有民族
      async getNationList() {
        this.nationList = await this.$ajax({
          url: '/common/queryNationList.do'
        })
      },
      // 获取在读的班级
      async getClass() {
        let res = await this.$ajax({
          url: '/class/queryClassPage.do',
          query: {
            classStatus: 'UN_GRADUATED',
            pageSize: 2000,
            pageNo: 1
          }
        })
        this.classList = res.list
      },
      // 提交
      async doSubmit(str) {
        let status = true
        await this.$refs.form.validate((v) => {
          status = v
        })
        if (!status) {
          this.$message.warning('请填写表单标红区域')
          return
        }
        let query = JSON.parse(JSON.stringify(this.form))
        // 处理出生日期
        if (query.birthday) {
          query.birthday = new Date(query.birthday).$Format('yyyy-MM-dd') + ' 00:00:00'
        }
        // 处理家庭信息 如果整行都没有填 那行的记录传空
        let homeStatus = false
        for (let i in query) {
          if (['father', 'mother', 'guardian'].includes(i)) {
            for (let j in query[i]) {
              if (query[i][j]) {
                homeStatus = true
              }
            }
            if (!homeStatus) {
              query[i] = null
            }
            homeStatus = false
          }
        }
        if (!query.father && !query.mother && !query.guardian) {
          this.$message.warning('必填一位家长信息')
          return
        }
        // 处理学生性质
        query.studentNature = this.form.studentNature[0]
        query.studentStatus = this.form.studentNature[1] || null
        // 学生性质为其它时必填备注
        if (query.studentNature === 'OTHER' && !this.form.remark) {
          this.$message.warning('请填写备注')
          return
        }
        let url = '/student/addStudent.do'
        let text = '新增成功'
        if (str === 'save') {
          url = '/student/updateStudent.do'
          text = '保存成功'
          query.id = this.$route.query.id
        }
        await this.$ajax({
          url,
          el: '.btn-' + str,
          noText: true,
          query: query
        })
        this.$remind(text)
        this.$router.push('/edu-affair/reading')
      },
      // 获取详情
      async getDetail() {
        let res = await this.$ajax({
          url: '/student/getStudent.do',
          el: '.reading-form',
          query: {
            id: this.$route.query.id
          }
        })
        this.form = res
        // 处理学生性质
        this.form.studentNature = [res.studentNature, res.studentStatus || null]

        this.form.father = {
          name: res.fatherName,
          company: res.fatherCompany,
          phone: res.fatherPhone
        }
        this.form.guardian = {
          name: res.guardianName,
          company: res.fguardianCompany,
          phone: res.guardianPhone
        }
        this.form.mother = {
          name: res.motherName,
          company: res.motherCompany,
          phone: res.motherPhone
        }
      },
      // 切换班级
      async changeClassFun() {
        if (!this.changeClassId) {
          this.$message.warning('请选择班级')
          return
        }
        await this.$ajax({
          url: '/student/updateStudentWithClass.do',
          el: '.btn-change',
          noText: true,
          query: {
            id: this.$route.query.id,
            classId: this.changeClassId
          }
        })
        this.getDetail()
        this.dialog = false
      },
      // 展现切换班级弹窗
      showDialog() {
        this.changeClassId = this.form.classId
        this.dialog = true
      },
      // 获取学生性质
      async getStudentNature() {
        this.natureList = await this.$ajax({
          url: '/student/queryStudentNatureList.do'
        })
      }
    },
    mounted() {
      if (this.editPage) {
        this.getDetail()
      }
      this.getNationList()
      this.getClass()
      this.getStudentNature()

    }
  }
</script>
